@import url(https://fonts.googleapis.com/css?family=Marck+Script|Sawarabi+Mincho|Ultra&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:"Gang of Three";src:url("/fonts/go3.ttf") format("truetype")}@media (max-width: 575px){#projBanner>h2{font-size:2em}#homeBanner>h2{font-size:4em}}@media (min-width: 576px){#projBanner>h2{font-size:3em}#homeBanner>h2{font-size:5em}#homeBanner>p:nth-child(2){font-size:1.5em}}@media (min-width: 768px){#projBanner>h2{font-size:4em}#homeBanner>h2{font-size:6em}}@media (min-width: 992px){#projBanner>h2{font-size:5em}#homeBanner>h2{font-size:7em}#homeBanner>p:nth-child(2){font-size:2em}}@media (min-width: 1200px){#projBanner>h2{font-size:6em}#homeBanner>h2{font-size:8em}}@-webkit-keyframes fadein{0%{opacity:0}100%{opacity:1}}@keyframes fadein{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes textleftslide{0%{left:-100vw}100%{left:10vw}}@keyframes textleftslide{0%{left:-100vw}100%{left:10vw}}@-webkit-keyframes textrightslide{0%{right:-100vw}100%{right:10vw}}@keyframes textrightslide{0%{right:-100vw}100%{right:10vw}}.fade-in{-webkit-animation:fadein 1s forwards;animation:fadein 1s forwards}.font-sm{font-family:"Sawarabi Mincho",sans-serif}.font-go3{font-family:"Gang of Three"}.text-border-black{text-shadow:-1px 0 black, 0 1px black, 1px 0 black, 0 -1px black}.text-border-white{text-shadow:-1px 0 white, 0 1px white, 1px 0 white, 0 -1px white}#root{position:relative;min-height:100vh;padding-bottom:5rem}#brand{position:fixed;top:0.875rem;left:1rem;z-index:1031;font-size:1rem;line-height:1.5}#brand>a>span{font-size:1rem}nav{-webkit-transition:1s;transition:1s}nav.navbar-bg{background-color:rgba(146,168,209,0.75)}nav .navbar-nav a{border:0.15em solid transparent}nav .navbar-nav a.active{border:0.15em solid white;border-radius:5%}footer{bottom:0}footer a{margin-left:0.5em}footer .fa-facebook{color:#3b5998}footer .fa-linkedin{color:#0077B5}footer .fa-github{color:white}footer .fa-envelope{color:orange}#homeBanner{height:100vh;-webkit-animation:fadein 2s forwards;animation:fadein 2s forwards}#homeBanner>h2{font-family:"Marck Script",cursive;line-height:1}#about figure{overflow:hidden;background-color:lightgray;width:10rem;height:10rem}#about figure img{top:-1rem}#projBanner{overflow-x:hidden;min-height:70vh}#projBanner>h2>span:nth-child(1){position:relative;-webkit-animation:textleftslide 1s forwards;animation:textleftslide 1s forwards}#projBanner>h2>span:nth-child(3){position:relative;float:right;right:-100vw;-webkit-animation:textrightslide 1s 1s forwards;animation:textrightslide 1s 1s forwards}#projects>h2{font-size:2.5rem}#projects figure{width:100%;padding-top:66.66667%}#projects figure img{top:0;left:0;width:100%;height:100%}#miscellaneous{padding-bottom:5rem;background-color:black;font-family:"Ultra",serif}#miscellaneous>a{opacity:0;font-size:80%}#miscellaneous>a:hover{opacity:1;color:white}

